.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  height: 9vh;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.3s ease-in-out, background-color 0.3s ease-in-out; /* Transitions for sliding and background color animations */
}

.transparent {
  background-color: transparent; /* Make the background transparent */
}

.black-bg {
  background-color: #000; /* Set the background color to black */
}

.slide-up {
  transform: translateY(-100%); /* Slide the navbar up and off the screen */
}
@media only screen and (max-width: 500px) and (max-height: 900px) {
    .navbar {
      height: 8vh;
    }
  }
  